import React from 'react';
import {Link} from 'react-router-dom';
import '../scss/signin.scss';
import axios from 'axios';
import {ConfirmContext} from '../../../context/ConfirmContext';
import {GlobalContext} from '../../../context/GlobalContext';
import {useNavigate}  from 'react-router-dom';
import CryptoJS from 'crypto-js';

export default function SignInComponent () {


    //     
    const navigate = useNavigate();
    const {login, setLogin, signin, setSigin} = React.useContext(GlobalContext);
    const {confirmModalOpen, isConfirmModal} = React.useContext(ConfirmContext);

    // ,  
    const [state, setState] = React.useState({
        user_id: '',
        user_pw: ''
    });
    const {user_id,user_pw} = state;

    //  
    const onChangeUser_id=(e)=>{
        setState({
            ...state,
            user_id: e.target.value
        })
    }

    //  
    const onChangeUser_pw=(e)=>{
        setState({
            ...state,
            user_pw: e.target.value
        })
    }



    //  
    // user_signin_action.jsp
    const onClickLogin=(e)=>{
        e.preventDefault();

        if(user_id===''){
            confirmModalOpen('  !');
        }
        else if(user_pw===''){
            confirmModalOpen('  !');
        }
        else {

            axios({
                // url:'/kurly/user_signin_action.jsp',
                url:'/kurly/kurly_user_signin_action_react.jsp',
                method: 'POST', //       POST
                data:{},
                params: {
                    "user_id": user_id,
                    "user_pw": user_pw
                }
            })
            .then((res)=>{
                
                if(res.status===200){
                    const result = res.data;
                    try {                    
                        if( result === -1 ){
                            confirmModalOpen(' . ');
                            setTimeout(function(){
                                window.location.pathname = `/signup`;
                            }, 1000);                                                       
                        }
                        else if( result === 0 ){
                            confirmModalOpen('   ');                        
                        }
                        else{
                            
                            let toDay = new Date();
                            toDay.setDate(toDay.getDate()+3); // 3 
                                            
                            setSigin({
                                user_id: user_id,
                                expires: toDay.getTime()
                            })
                            
                            const obj = { //   
                                user_id: user_id,
                                expires: toDay.getTime()
                            }
                            console.log(obj);

                            //  Encryption
                            let encryption = CryptoJS.AES.encrypt(JSON.stringify(obj), signin.signinKey).toString(); //  JSON.stringify 
                            // encryption = encryption.replaceAll('=',' ') // = 
                            localStorage.setItem(signin.signinKey, JSON.stringify(obj)); //         

                            confirmModalOpen(' .');
                            // setTimeout(function(){
                            //      window.location.pathname = `/main`;
                            // }, 1000); 
                        }
                    } catch (error) {
                        console.log( error );
                    }
                }
                
            })
            .catch((err)=>{
                console.log(`AXIOS ! ${err} `)
            });  

        }

    }


    //  
    React.useEffect(()=>{
        //    
        let encryption='';
        let result = '';

        if( localStorage.getItem(signin.signinKey)!==null ){

           //     
            // encryption = localStorage.getItem(signin.signinKey); 
            result = JSON.parse(localStorage.getItem(signin.signinKey)); 

            //   ()
            // Decryption
            // const decryption = CryptoJS.AES.decrypt(encryption, signin.signinKey);
                            
            //    () 
            // const decryptionData = JSON.parse(decryption.toString(CryptoJS.enc.Utf8));
            // console.log(decryptionData);

            //  result = decryptionData;
             if( new Date() > result.expires){
                alert(' . .');
                setSigin({
                    ...signin,
                    user_id: '',
                    expires: ''
                })
                 localStorage.removeItem(signin.signinKey); //    
             } 
             else{
                setSigin({
                    ...signin,
                    user_id: result.user_id,
                    expires: result.expires
                 })
             }
        }

    },[signin]);

    return (
        <main id='signIn'>
            <section id="secino1">
                <div className="container">
                    <div className="title">
                        <h2></h2>
                    </div>
                    <div className="content">
                        <form autoComplete='off'>
                            <ul>
                                <li>
                                    <input 
                                    onChange={onChangeUser_id}
                                    type="text" 
                                    name='user_id' 
                                    id='userId' 
                                    value={user_id} 
                                    placeholder=' ' 
                                    />
                                </li>
                                <li>
                                    <input 
                                    onChange={onChangeUser_pw}
                                    type="password" 
                                    name='user_pw' 
                                    id='userPw' 
                                    value={user_pw} 
                                    placeholder=' ' 
                                    />
                                </li>
                                <li>
                                    <span>
                                        <Link to="/idSearch"> </Link>
                                        <i>|</i>
                                        <Link to="/pwSearch"> </Link>
                                    </span></li>
                                <li><button  onClick={onClickLogin}></button></li>
                                <li><Link to="/signup"></Link></li>
                            </ul>
                        </form>
                    </div>
                </div>
            </section>
        </main>
    );
};